/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
nav {
  background-color: #b33f30;
  padding: 1rem 0;
}

nav h2 {
  display: inline;
  margin: 0 2vw;
  font-size: 1.25rem;
  color: black;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  font-display: bold;
}

nav a {
  margin: 0 2vw;
  font-size: 1rem;
  color: white;
  text-decoration: none;
}
nav a:hover {
  background-color: #33658A;
  height: 100%;
}
nav a:visited {
  color: rgb(53, 216, 216);
}

header h1 {
  font-size: 7vw;
  text-align: center;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: white;
  padding: 12% 0 3%;
  text-shadow: 5px 5px 30px black;
}

header p {
  font-size: 3vw;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  text-align: center;
  color: white;
  text-shadow: 5px 5px 30px black;
}

h3 {
  font-size: 1.5rem;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

#searchContainer, #resultsContainer {
  color: white;
  background-color: #33658A;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  padding: 2vw;
  margin: 2vw;
  font-size: 3vw;
}

form {
  padding: 1rem 0 1.5rem;
  position: relative;
}

label {
  font-size: 1.25rem;
}

input {
  margin: 1vw 0;
  width: 98%;
  padding: .75vw;
  font-size: 1.25vw;
}

button {
  border-radius: 0;
  border: none;
  background-color: #f9c411;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: white;
  padding: .75vw;
  font-size: 1.25vw;
}

form button {
  position: absolute;
  right: 0;
}

button[disabled] {
  background-color: #f9e39f;
}

#resultsContainer {
  font-size: 1rem;
}

#resultsContainer h3 {
  margin-bottom: 3.5vw;
}

.bookResult {
  background-color: white;
  color: black;
  padding: 1.75vw;
  font-size: 1rem;
}

.bookResult:not(:last-of-type) {
  margin-bottom: 2vw;
}

.bookResult h4 {
  font-size: 1.25rem;
  margin-bottom: 1vw;
}

.bookResult .row:first-of-type {
  margin-bottom: 1vw;
}

.bookResult img {
  display: inline-block;
  box-sizing: border-box;
  max-width: 100px;
  max-height: auto;
  margin-right: 2vw;
}

.aboutBook {
  box-sizing: border-box;
  display: inline-block;
  width: 75%;
  padding: 0;
  /* margin: 1.75vw 0 0 1.75vw; */
}

.btnDiv {
  box-sizing: border-box;
  float: right;
}

.btnDiv button:first-of-type {
  margin-right: 1.75vw;
}

@media screen and (min-width: 480px) {
  .bookResult .row:nth-of-type(2) {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: flex-start;
            align-items: flex-start;
  }
}
